.cover-container{
    height: 100vh; /*ocupe toda la altura*/
    width: 100%;
    object-fit: contain;  /*la misma anchhura siempre*/
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.video{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1; /*mandalo al fondo*/
}

.cover-container > h1 {
    color: white;
    font-size: 100px;
}

.cover-container > p {
    margin-top: 8px;
    color: white;
    font-size: 32px;
}

.cover-container > a:hover {
  transform: scale(1.1);
  transition: 0.2s;
  background-color: white;
  color: #2A77C6;
  border-color: white;
}


/*responsive*/
@media screen and (max-width: 960px) {
    .cover-container > h1 {
      font-size: 70px;
      text-align: center;
    }
  
    .cover-container > p {
      text-align: center;
      font-size: 22px;
      width: 85%;
    }
  }